* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: 0;
}

body > div {
  overflow-x: hidden;
}

ul {
  margin-bottom: 0;
}

body::-webkit-scrollbar {
  display:none;
}

body {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  scroll-behavior: smooth;
}

.Social {
  margin-top: 20px;
  margin-bottom: -20px;
  margin-left: 20px;
  margin-right: 20px;
}

.btnBurger {
  position: absolute;
  top: 0;
  left: 0px;
}

.MenuBurger {
  width: 200px;
  height: 200px;
}

section#sec-1 {
  width: 100%;
  height: 100vh;
  background: linear-gradient(to bottom, #fca469, #f58e19);
}

section#sec-1 .marbre {
  height: 100%;
  background-image: url(../img/fond-marbre.png);
  background-position: right top;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}

section#sec-1 .marbre .container {
  text-align: center;
  color: white;
}

section#sec-1 .marbre .container h1 {
  font-size: 5em;
}

section#sec-1 .marbre .container p {
  font-size: 1.5em;
  margin-bottom: 50px;
}

section#sec-1 .marbre .container a.title-btn {
  background-color: white;
  padding: 1em 2em;
  border-radius: 50px;
  color: #ff8e0d;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
}

.title-btn {
  cursor: pointer;
  border: none;
  background-color: white;
  background-image: linear-gradient(135deg, #ffffff 60%, #206992 80%);
  background-size: 600% 100%;
  background-position: left;
  background-repeat: no-repeat;
  transition: 500ms;
}

.title-btn:hover {
  background-position: right;
}

