section#sec-2 > h1 {
  text-align: center;
  color: #181b3a;
  font-size: 2.5em;
  font-weight: 500;
  margin-top: 30px;
}

section#sec-2 {
  padding: 80px 10px;
}

section#sec-2 > P {
  max-width: 800px;
  text-align: center;
  color: #637282;
  margin: 0 auto;
}

section#sec-2 .container {
  max-width: 1400px;
  justify-content: center;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  margin: 70px auto 0 auto;
}

section#sec-2 .container .competences {
  text-align: center;
  margin: 25px 0;
}

section#sec-2 .container .competences h2 {
  color: #181b3a;
  font-weight: 500px;
  margin: 10px 0;
}

section#sec-2 .container .competences p {
  color: #637282;
  font-weight: 400px;
  padding: 0 10px;
}

/* responsive */
@media screen and (max-width: 600px) {
  section#sec-2 > h2 {
    font-size: 2em;
  }
  /* section#sec-2 .container {
        flex-direction: column;
    } */
  section#sec-2 .container .competences p {
    max-width: 95%;
  }
}

.i {
  color: #f58e19;
}

.codeIt {
  font-size: 10em;
}

section#sec-3 {
  background: linear-gradient(to bottom, #f58e1900, #f8690934);
  padding: 32px 0px;
}

section#sec-3 .container {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

section#sec-3 .container .left {
  max-width: 500px;
}

section#sec-3 .container .right {
  max-width: 50%;
}

section#sec-3 .container .right h1 {
  font-size: 2.5em;
  color: #181b3a;
  margin-bottom: 30px;
}

section#sec-3 .container .right p {
  font-size: 1.5em;
  color: #637282;
}

section#sec-3 .container .right p:nth-of-type(2) {
  margin-bottom: 60px;
}

section#sec-3 .container .right a.dl-btn {
  background-color: white;
  padding: 1em 1em;
  border-radius: 50px;
  color: #ff8e0d;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
}

.dl-btn {
  cursor: pointer;
  border: none;
  background-color: white;
  background-image: linear-gradient(135deg, #ffffff 60%, #206992 80%);
  background-size: 600% 100%;
  background-position: left;
  background-repeat: no-repeat;
  transition: 500ms;
}

@media screen and (max-width: 7450px) {
  .dl-btn {
    margin: 10px;
  }
}

.dl-btn:hover {
  background-position: right;
}

@media screen and (max-width: 600px) {
  section#sec-3 > h1 {
    font-size: 1.5em;
  }
  section#sec-3 .container {
    flex-direction: column;
  }
  section#sec-3 .container .right p {
    max-width: 95%;
  }
  section#sec-3 .container .left i {
    margin-bottom: 50px;
  }
}

